import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Injectable } from '@angular/core';
import {Observable} from 'rxjs';
import { ConsultaDTO } from '../material-component/send-xml/send-xml.component';

@Injectable({
  providedIn: 'root'
})
export class NominaelectronicaService {
  url= "http://localhost:9090/";
  endpoint1 = "thctrliq/nomelec/";
  endpoint2 = "thctrliq/download/";
  getSendXml = "thctrliq/sendXML";
  getSendErrors= "nomelec/sendeErrors"
  getObtenErrors="nomelec/xmlerror/"

  constructor(private http: HttpClient) { }

  generarXml(fhi:String, fhf:String):Observable<any>{
    return this.http.get<any>(this.url + this.endpoint1 + fhi +"/"+ fhf);
  }
  downloadExcelcpto(fhi:String, fhf:String):Observable<any>{
    const headers = new HttpHeaders().set('Content-Type', 'application/json');
    return this.http.get<any>(this.url+ this.endpoint2 + fhi + "/" + fhf, {headers, responseType:'blob' as 'json'});
  }
  //se envia nominas al web services DIAN
  enviarNominasWs(consultaDTO:ConsultaDTO):Observable<any>{
    return this.http.post<ConsultaDTO>( this.url + this.getSendXml, consultaDTO);
  }
  //Se envia nominas con error al web service DIAN
  enviarErrorsNominaWs():Observable<any>{
    return this.http.get<any>(this.url + this.getSendErrors);
  }
  //Se generan las nominas con estatus en thtracking igual 99
  /* NOTA si hay posible estados se debe tener presente 
  modificar este servicio. Esta con los estados disponibles por la DIAN */
  generarXmlErrors(fhi:String, fhf:String):Observable<any>{
    return this.http.get<any>(this.url + this.getObtenErrors+fhi +"/"+ fhf)
  }
}
