import { Component, OnInit } from '@angular/core';
import { MessageService} from 'primeng/api';
import { NominaelectronicaService } from '../../services/nominaelectronica.service';
import { format } from 'date-fns';


@Component({
  selector: 'app-send-error',
  templateUrl: './send-error.component.html',
  styleUrls: ['./send-error.component.css'],
  providers: [MessageService]
})
export class SendErrorComponent implements OnInit {

  value: Date;
  value1: Date;
   fhi: string;
   fhf: string;
  constructor(private messageService: MessageService, private nominaElectronica: NominaelectronicaService) { }

  ngOnInit(): void {
  }

  GenXmlErrors(){
    if(this.value1 >this.value){
      this.fhi = format(this.value, "yyyy-MM-dd");
      this.fhf = format(this.value1, "yyyy-MM-dd");
      
      this.nominaElectronica.generarXmlErrors(this.fhi, this.fhf).subscribe(data =>{
       data;
        this.messageService.add({severity:'success', summary: 'Service Message', detail: 'Archivos Generado Correctamente'});
      });
      }else if(this.value1 == null || this.value == null){
        this.messageService.add({severity:'error', summary: 'Error', detail: 'Fechas Vacias.'});
      }
      else if(this.value>this.value1){
        this.messageService.add({severity:'error', summary: 'Error', detail: 'La fecha final es menor, que fecha inicial.' });
      }else{
        this.messageService.add({severity:'error', summary: 'Error', detail: 'No hay liquidación disponible en el rango de fecha seleccionada.'});
      }
  }

}
