import { Component, OnInit } from '@angular/core';
import {NominaelectronicaService} from '../../services/nominaelectronica.service';
import { MessageService} from 'primeng/api';
import { parseJSON } from 'date-fns';
import { parse } from 'date-fns';
import { format } from 'date-fns'
export interface MensajeDTO{
  tipoalerta: number,
  coderror: number,
  msjerror: string,
  idvalidacion:number
  object: object
}
@Component({
  selector: 'app-nomina-electronica',
  templateUrl: './nomina-electronica.component.html',
  styleUrls: ['./nomina-electronica.component.css'],
  providers: [MessageService]
})
export class NominaElectronicaComponent implements OnInit {
  mensaje: MensajeDTO;
  loading: boolean = true;
  value: Date;
  value1: Date;
   fhi: string;
   fhf: string;
  public es: any;
  constructor(private messageService: MessageService ,private nomimaelectronica:NominaelectronicaService) { 
  
    this.es = {
      firstDayOfWeek: 1,
      dayNames: [ "domingo","lunes","martes","miércoles","jueves","viernes","sábado" ],
      dayNamesShort: [ "dom","lun","mar","mié","jue","vie","sáb" ],
      dayNamesMin: [ "D","L","M","X","J","V","S" ], 
      monthNames: [ "enero","febrero","marzo","abril","mayo","junio","julio","agosto","septiembre","octubre","noviembre","diciembre" ],
      monthNamesShort: [ "ene","feb","mar","abr","may","jun","jul","ago","sep","oct","nov","dic" ],
      today: 'Hoy',
      clear: 'Borrar'
  };

  }
      
  ngOnInit(): void {
    
  }
  
  onReject() {
    this.messageService.clear('c');
}

clear() {
    this.messageService.clear();
}

 genfile(){
  if(this.value1 >this.value){
  this.fhi = format(this.value, "yyyy-MM-dd");
  this.fhf = format(this.value1, "yyyy-MM-dd");
  this.nomimaelectronica.generarXml(this.fhi,this.fhf).subscribe(data=>{
    this.mensaje= data;
    this.messageService.add({severity:'success', summary: 'Service Message', detail: 'Archivos Generado Correctamente'});
   });
  }else if(this.value1 == null || this.value == null){
    this.messageService.add({severity:'error', summary: 'Error', detail: 'Fechas Vacias.'});
  }
  else{
    this.messageService.add({severity:'error', summary: 'Error', detail: 'La fecha final es menor, que fecha inicial.' });
  }
 }

 manageExcelFile(response: any, fileName: string):void{
   const dataType= response.type;
   const binaryData= [];
   binaryData.push(response);

   const filtePath = window.URL.createObjectURL(new Blob(binaryData,{type:dataType}));
   const downloadLink= document.createElement('a');
   downloadLink.href = filtePath;
   downloadLink.setAttribute('download', fileName);
   document.body.appendChild(downloadLink);
   downloadLink.click();
 }

 gendownloadCptodian(){
   if(this.value1 >this.value){
    this.fhi = format(this.value, "yyyy-MM-dd");
    this.fhf = format(this.value1, "yyyy-MM-dd");
    const fileName =  `equivalenciaDian_${format(this.value, "yyyy-MM")}`;
     this.nomimaelectronica.downloadExcelcpto(this.fhi,this.fhf).subscribe(data=>{
      this.manageExcelFile(data, fileName);
      this.messageService.add({severity:'success', summary: 'Service Message', detail: 'Equivalencia Descargado Correctamente.'});
     });
   }else if(this.value == null || this.value1 == null){
     console.log('campos vacios')
    this.messageService.add({severity:'error', summary: 'Error', detail: 'Fechas Vacias.'});
  }
  else{
    this.messageService.add({severity:'error', summary: 'Error', detail: 'La fecha final es menor, que fecha inicial.' });
  }
 }

}
