import { Component, OnInit, Input, ViewChild, EventEmitter, Output } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router, RouterModule } from '@angular/router';
import { FilterMetadata,LazyLoadEvent } from 'primeng/api';
import { Table } from 'primeng/table';
import { Subscription } from 'rxjs';
import {ConceptosdianService} from '../../services/conceptosdian.service';
import { FunctionServiceService } from '../../services/function-service.service';
import { MensajeconfirmacionComponent } from '../mensajeconfirmacion/mensajeconfirmacion.component';

export interface PaginatorEvent {
  queryParams: string;
  params: any;
}

export interface Cptosdian{
  descripcion: string,
  idDian: string,
  xpath: string,
  thcptos: number
  acciones:string
}
export enum PaginatorParamsHandling {
  QueryParams,
  Params,
}

enum SortOrder {
  Asc = 1,
  Desc = -1,
}

@Component({
  selector: 'app-conceptos-dian',
  templateUrl: './conceptos-dian.component.html',
  styleUrls: ['./conceptos-dian.component.css']
})


export class ConceptosDianComponent implements OnInit {

  @Input() lazy: boolean = true;
  @Input() paramsHandling: PaginatorParamsHandling = PaginatorParamsHandling.QueryParams;
  @Output() onPager = new EventEmitter<LazyLoadEvent>(); // TODO, change to PaginatorEvent
  @ViewChild('table', { static: true }) public table?: Table;
   cptosDian : Cptosdian[];
   first = 0;
   loading: boolean = true;
   rows = 10;
  dt: any;
   
  private subs: Subscription = new Subscription();
  private readonly SORT_FIELD: string = 'obField';
  private readonly SORT_ORDER: string = 'order';
  private readonly SORT_ORDER_NAME_ASC: string = 'asc';
  private readonly SORT_ORDER_NAME_DESC: string = 'desc';
  private readonly ROWS: string = 'size';
  private readonly PAGE: string = 'page';

  constructor(private conceptosdianServices: ConceptosdianService, private dialog:MatDialog,
    public snackbar:MatSnackBar, private route: Router ){}
 
  ngOnInit(): void {
    this.CargaThcptosdian();
  }

  CargaThcptosdian(){
    this.conceptosdianServices.getCptoDian().subscribe(data =>
      {
        this.cptosDian = data;
        this.loading = false;
        this.cptosDian.forEach(Cptosdian => Cptosdian.idDian);
      });
  }
  applyFilterGlobal($event, stringVal) {
    this.dt.filterGlobal(($event.target as HTMLInputElement).value, 'contains');
  }
  next() {
    this.first = this.first + this.rows;
}

prev() {
    this.first = this.first - this.rows;
}

reset() {
    this.first = 0;
}

isLastPage(): boolean {
    return this.cptosDian ? this.first === (this.cptosDian.length - this.rows): true;
}

isFirstPage(): boolean {
    return this.cptosDian ? this.first === 0 : true;
}

clear(table: Table) {
  table.clear();
}
eliminarThcptosdian(index : number){
  const dialogRef = this.dialog.open(MensajeconfirmacionComponent,{
    width: '250px',
    data:{mensaje:'¿Desea eliminar el concepto dian?'}
  });
  dialogRef.afterClosed().subscribe(result =>{
    if(result === 'aceptar'){
      this.conceptosdianServices.eliminarcptodian(index).subscribe();
      this.CargaThcptosdian();
      this.snackbar.open('El concepto dian eliminado correctamente!', '',{
      duration:4000
      });
      
    }
  });
}


}
