import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import {ConceptosdianService} from '../../services/conceptosdian.service';
import { MatSnackBar } from '@angular/material/snack-bar';

export interface Cptos1{
  algoriId: number,
  abrev: string,
  agrubase: string ,
  caractLiq: number,
  diasFactor: number,
  diasPres: number,
  id: number,
  indBasico: number,
  k1Cod: number,
  k2Desc: string,
  Naturaleza:string ,
  porce: DoubleRange,
  Vlrunit: DoubleRange,
  gruposId: number
}
export interface cptos{
  name: string,
  code: number
}
export interface Thcptosdian{
  thcptosdianDescripcion: string,
  thcptosdianIdDian: string,
  thcptosdianXpath: string,
  thcptosId_Thcptos:number
}
@Component({
  selector: 'app-cptosdian-edit',
  templateUrl: './cptosdian-edit.component.html',
  styleUrls: ['./cptosdian-edit.component.css']
})
export class CptosdianEditComponent implements OnInit {

    opc1: any[];
    myForm : FormGroup;
   selectedCptos: any[];
   cpot: any[];
   value1: any;

   value2: any;

   value3: any;

   value8: any;
    idcptosdian:  any;
    accion = 'Crear'
  constructor(private conceptodianService: ConceptosdianService, private fb: FormBuilder,
    private route:Router, public snackbar:MatSnackBar,
    private aRoute: ActivatedRoute) { 
    
      /* OBJECTO DE INSERT*/
      this.myForm = this.fb.group({
        thcptosdianDescripcion: [''],
        thcptosdianIdDian: [''],
        thcptosdianXpath: [''],
        thcptosId_Thcptos:['']
      });  
      
      /*UPDATE DE REGISTRO */
      const idParam = 'id';
      this.idcptosdian = this.aRoute.snapshot.params[idParam];
    /*RELLENO SELECT CONCEPTOS */
    let filtered : any[] = [];
    this.conceptodianService.getCptos().subscribe(data => {
      for(let i=0; i < data.length; i++){  
        this.cpot = ([{name: data[i].thcptosK2Desc, code: data[i].thcptosId}]);
        filtered.push({
          name:data[i].thcptosK2Desc,
          code:data[i].thcptosId
        })
         
      }
      this.selectedCptos = filtered;
    });
  }

  ngOnInit(): void {
      if(this.idcptosdian !== undefined){
        this.accion='Editar';
        this.getThcptodianId();
      }
   }

   getThcptodianId(){
     this.conceptodianService.getCptoDianId(this.idcptosdian).subscribe(data =>{
       this.myForm.patchValue({
        thcptosdianDescripcion: data.thcptosdianDescripcion,
        thcptosdianIdDian: data.thcptosdianIdDian,
        thcptosdianXpath: data.thcptosdianXpath,
        thcptosId_Thcptos: data.thcptosId_Thcptos

       });
     });
   }

  guardarCptoDian(){
    const  thcptosdian: Thcptosdian ={
      thcptosdianDescripcion: this.myForm.get('thcptosdianDescripcion').value,
      thcptosdianIdDian: this.myForm.get('thcptosdianIdDian').value,
      thcptosdianXpath: this.myForm.get('thcptosdianXpath').value,
      thcptosId_Thcptos: this.myForm.get('thcptosId_Thcptos').value

    };
    this.conceptodianService.agregarThcptosdian(thcptosdian).subscribe(data =>
      this.snackbar.open('El concepto dian almacenado correctamente!', '',{
        duration:4000
        }));
    this.route.navigate(['/conceptos-dian']);
    
  }
 
 
}
