import { Component, OnInit } from '@angular/core';
import { MessageService} from 'primeng/api';
import {FormBuilder, FormGroup } from '@angular/forms';
import { NominaelectronicaService } from '../../services/nominaelectronica.service';
import { format } from 'date-fns';

export interface ConsultaDTO{
  ip: string,
  idTipoIdent: number,
  identificacion: string,
  fechaExpDoc:string,
  nombres: string,
  apellido1: string,
  apellido2: string,
  token: string,
  idValidacion: number
}
@Component({
  selector: 'app-send-xml',
  templateUrl: './send-xml.component.html',
  styleUrls: ['./send-xml.component.css'],
  providers: [MessageService]
})
export class SendXmlComponent implements OnInit {
  myForm : FormGroup;
  value1: Date;
   fhf: string;
  constructor(private messageService: MessageService, private nominaElectronica: NominaelectronicaService,  private fb:FormBuilder) { 

       /* OBJECTO DE INSERT*/
       this.myForm = this.fb.group({
        ip: [''],
        idTipoIdent: [''],
        identificacion: [''],
        fechaExpDoc:[''],
        nombres:[''],
        apellido1:[''],
        apellido2:[''],
        token:[''],
        idValidacion:['']
      }); 
  }
    
  ngOnInit(): void {
  }

  ConsumeWebServices(){
    if(this.value1 != null){
      const consulta: ConsultaDTO ={
        ip: null,
        idTipoIdent: null,
        identificacion: null,
        fechaExpDoc: format(this.value1, "yyyy-MM-dd"),
        //this.myForm.get('ffsend').value,
        nombres:null,
        apellido1:null,
        apellido2:null,
        token:null,
        idValidacion:null
        
      }; 
      this.nominaElectronica.enviarNominasWs(consulta).subscribe(data =>{
       data;
        this.messageService.add({severity:'success', summary: 'Service Message', detail: 'Archivos Generado Correctamente'});
      });
      }else if(this.value1 == null){
        this.messageService.add({severity:'error', summary: 'Error', detail: 'Fecha no seleccionada.'});
      }else{
        this.messageService.add({severity:'error', summary: 'Error', detail: 'No hay Archivos para enviar en la fecha seleccionada.'});
      }
  }

  
}
