import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import { Table } from 'primeng/table';
import { format } from 'date-fns'
import { ArchivosxmlService } from '../../services/archivosxml.service';

export interface Thnome{
  thdocid: number,
  prefijo: String,
  fechaEmision: Date,
  nroDocumento:String,
  nombreEmpleado:String,
  totalComprobante:number,
  estado:String

}
@Component({
  selector: 'app-archivos-xml',
  templateUrl: './archivos-xml.component.html',
  styleUrls: ['./archivos-xml.component.css']
})
export class ArchivosXmlComponent implements OnInit {
  thnome : Thnome[];
  first = 0;
  loading: boolean = true;
  rows = 10;
  constructor(private archivosxmlservice:ArchivosxmlService, private dialog:MatDialog,
    public snackbar:MatSnackBar, private route: Router ,
    private aRoute:ActivatedRoute) { }

  ngOnInit(): void {
    this.CargaThnome();
    
  }
  CargaThnome(){
    this.archivosxmlservice.getThnome().subscribe(data =>{
      this.thnome = data;
      this.loading= false;
      this.thnome.forEach(Thnome => Thnome.nroDocumento)
    });
  }
  ObtenerCertificadoIndividual(nameFile: string){
    this.archivosxmlservice.getNominaElctronicaPdf(nameFile).subscribe(data =>{
      var file = new Blob([data], {type:'application/pdf'});
      var fileUrl = URL.createObjectURL(file);

      window.open(fileUrl);
      var a = document.createElement('a');
      a.href= fileUrl;
      a.target= '_blank';
      a.download= 'NominaElectronica.pdf';
      document.body.appendChild(a);
      a.click();
    });
  }
  next() {
    this.first = this.first + this.rows;
}

prev() {
    this.first = this.first - this.rows;
}

reset() {
    this.first = 0;
}

isLastPage(): boolean {
    return this.thnome? this.first === (this.thnome.length - this.rows): true;
}

isFirstPage(): boolean {
    return this.thnome ? this.first === 0 : true;
}

clear(table: Table) {
  table.clear();
}
}
