import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { Thcptosdian } from '../material-component/cptosdian-edit/cptosdian-edit.component';

@Injectable({
  providedIn: 'root'
})
export class ConceptosdianService {

  url= "http://localhost:9090/";
  endpoint1 = "thcptosdian";
  endpoint2 = "thcptos";
  constructor(private http: HttpClient) { }

  getCptoDian(): Observable<any>{
    return this.http.get<any>(this.url + this.endpoint1);
  }
  getCptoDianId(id:number){
    return this.http.get<any>(this.url + this.endpoint1+"/"+id);
  }
  getCptos(): Observable<any>{
    return this.http.get<any>(this.url + this.endpoint2);
  }
  agregarThcptosdian(thcptosdian: Thcptosdian):Observable<any>{
     return this.http.post<Thcptosdian>(this.url + this.endpoint1, thcptosdian);
  }
  actualizarThcptosdian(thcptosdian: Thcptosdian):Observable<any>{
    return this.http.put<Thcptosdian>(this.url + this.endpoint1, thcptosdian);
  }
  eliminarcptodian(id:number):Observable<{}>{
   
    return this.http.delete(this.url + this.endpoint1+"/"+id);
  }
}
