import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ArchivosxmlService {

  url= "http://localhost:9090/";
  endpoint='api/v1/thnome';
  getcertificado='thctrliq/cert/imprimir/';
  constructor(private http: HttpClient) { }

  getThnome():Observable<any>{
    return this.http.get<any>(this.url + this.endpoint);
  }
  getNominaElctronicaPdf(path:String):Observable<any>{
    const headers = new HttpHeaders({'Content-Type': 'application/json'})
    return this.http.get<any>(this.url + this.getcertificado+"/"+path, {headers: headers, responseType: 'blob' as 'json'});
  }
}
